.popup {
  position: fixed;
  z-index: 20;
  background-color: rgba($black, .7);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 15px;
  overflow: auto;
  visibility: hidden;
  opacity: 0;
  @include vertical;
  @include ready {
    @include tr(opacity .3s, visibility .3s); }
  &.is-active {
    visibility: visible;
    opacity: 1; }
  &__inner {
    display: inline-block;
    vertical-align: middle;
    font-size: $fz;
    position: relative;
    background-color: $white;
    width: 100%;
    max-width: 450px;
    padding: 60px;
    @include xl {
      padding: 50px; }
    @include lg {
      padding: 40px; }
    @include md {
      padding: 30px; }
    @include sm {
      padding: 20px; }
    @include xs {
      padding: 15px; }
    @include sm {
      font-size: $fz-sm-px; } } }
