.testimonials {
  position: relative;
  overflow: hidden;
  &__slider {
    position: absolute;
    top: 0;
    bottom: 0;
    left: -100px;
    width: calc(#{percentage(1065/1920)} + 100px);
    z-index: 1;
    @include flex(center, normal, column);
    @include sm {
      width: 40%;
      left: 0; }
    @include xs {
      width: 30%; } }
  &__content-in {
    max-width: calc(#{percentage(5/12)} - 30px);
    margin-left: auto;
    @include flex();
    overflow: hidden;
    position: relative;
    @include sm {
      max-width: 60%;
      padding-left: 10px; }
    @include xs {
      max-width: 70%; } }
  &__content {
    min-height: 440px;
    @include flex(center, normal, column);
    .container {
      width: 100%; }
    @include sm {
      min-height: 350px; }
    @include xs {
      min-height: 250px; } }
  &__controls {
    max-width: calc(#{percentage(5/12)} - 30px);
    margin-left: auto;
    @include sm {
      max-width: 60%;
      padding-left: 10px; }
    @include xs {
      max-width: 70%; } }
  &__content-item {
    flex: 0 0 100%;
    visibility: hidden;
    opacity: 0;
    @include tr(opacity .3s, visibility .3s);
    .testimonial-content {
      opacity: 0;
      transform: translate(0, 30px); }
    &.is-active {
      visibility: visible;
      opacity: 1;
      .testimonial-content {
        visibility: visible;
        opacity: 1;
        transform: translate(0, 0); } } }

  &__img {
    @include cover;
    padding-top: 135%;
    @include tr(filter .3s, transform .3s);
    filter: grayscale(100%); } }

.testimonial-content {
  @include tr(opacity .7s,transform .7s);
  &__social {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__text {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } } }
